"use client"

import { useState } from "react"
import { SellerSidebar } from "@/components/seller/sidebar"
import { SellerHeader } from "@/components/seller/header"
import { Search, Send, Paperclip, Phone, Mail, Star, MoreVertical, Reply, Forward, Trash2, Archive } from "lucide-react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Textarea } from "@/components/ui/textarea"
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"

interface Message {
  id: number
  customer: {
    name: string
    email: string
    avatar?: string
  }
  subject: string
  message: string
  timestamp: string
  status: "unread" | "read" | "replied"
  priority: "low" | "medium" | "high"
  orderId?: string
  rating?: number
}

export default function SellerMessagesPage() {
  const [messages, setMessages] = useState<Message[]>([
    {
      id: 1,
      customer: {
        name: "Sarah Johnson",
        email: "sarah.j@email.com",
        avatar: "/images/customer1.jpg"
      },
      subject: "Order Received - Thank You!",
      message: "Hi! I received my order today and the product is exactly as described. Thank you!",
      timestamp: "2 hours ago",
      status: "unread",
      priority: "high",
      orderId: "#12345",
      rating: 5
    },
    {
      id: 2,
      customer: {
        name: "Mike Chen",
        email: "mike.chen@email.com"
      },
      subject: "Question about product specifications",
      message: "Can you provide more details about the material used in the wireless headphones?",
      timestamp: "2 hours ago",
      status: "read",
      priority: "medium"
    },
    {
      id: 3,
      customer: {
        name: "Emily Davis",
        email: "emily.d@email.com"
      },
      subject: "Shipping inquiry",
      message: "When can I expect my order to be delivered? The tracking shows it's still in transit.",
      timestamp: "1 day ago",
      status: "replied",
      priority: "low",
      orderId: "#12347"
    }
  ])

  const [selectedMessage, setSelectedMessage] = useState<Message | null>(null)
  const [searchTerm, setSearchTerm] = useState("")
  const [replyText, setReplyText] = useState("")
  const [showReplyBox, setShowReplyBox] = useState(false)

  const filteredMessages = messages.filter(msg =>
    msg.customer.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    msg.subject.toLowerCase().includes(searchTerm.toLowerCase()) ||
    msg.message.toLowerCase().includes(searchTerm.toLowerCase())
  )

  const markAsRead = (messageId: number) => {
    setMessages(messages.map(msg =>
      msg.id === messageId ? { ...msg, status: "read" as const } : msg
    ))
  }

  const deleteMessage = (messageId: number) => {
    setMessages(messages.filter(msg => msg.id !== messageId))
    setSelectedMessage(null)
  }

  const archiveMessage = (messageId: number) => {
    setMessages(messages.filter(msg => msg.id !== messageId))
    setSelectedMessage(null)
  }

  const sendReply = () => {
    if (selectedMessage && replyText.trim()) {
      alert(`Reply sent to ${selectedMessage.customer.name}: ${replyText}`)
      setMessages(messages.map(msg =>
        msg.id === selectedMessage.id ? { ...msg, status: "replied" as const } : msg
      ))
      setReplyText("")
      setShowReplyBox(false)
    }
  }

  const getStatusColor = (status: string) => {
    switch (status) {
      case "unread": return "bg-blue-100 text-blue-800 dark:bg-blue-900 dark:text-blue-200"
      case "read": return "bg-gray-100 text-gray-800 dark:bg-gray-900 dark:text-gray-200"
      case "replied": return "bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200"
      default: return "bg-gray-100 text-gray-800"
    }
  }

  const getPriorityColor = (priority: string) => {
    switch (priority) {
      case "high": return "bg-red-100 text-red-800 dark:bg-red-900 dark:text-red-200"
      case "medium": return "bg-yellow-100 text-yellow-800 dark:bg-yellow-900 dark:text-yellow-200"
      case "low": return "bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200"
      default: return "bg-gray-100 text-gray-800"
    }
  }

  return (
    <div className="flex bg-background">
      <SellerSidebar />

      <div className="flex-1 flex flex-col">
        <SellerHeader />

        <main className="flex-1 p-8">
          <div className="mb-8 flex justify-between items-center">
            <div>
              <h1 className="text-3xl font-bold mb-2">Messages</h1>
              <p className="text-muted-foreground">Communicate with your customers</p>
            </div>
            <div className="flex gap-4 items-center">
              <div className="relative">
                <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-muted-foreground" size={18} />
                <Input
                  placeholder="Search messages..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  className="pl-10 w-64"
                />
              </div>
              <Button variant="outline">
                <Archive className="mr-2 h-4 w-4" />
                Archive All
              </Button>
            </div>
          </div>

          <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
            {/* Messages List */}
            <div className="lg:col-span-1">
              <Card>
                <CardHeader>
                  <CardTitle className="text-lg">Inbox</CardTitle>
                </CardHeader>
                <CardContent className="p-0">
                  <div className="divide-y divide-border">
                    {filteredMessages.map((message) => (
                      <div
                        key={message.id}
                        className={`p-4 cursor-pointer hover:bg-muted transition-colors ${
                          selectedMessage?.id === message.id ? "bg-muted" : ""
                        } ${message.status === "unread" ? "border-l-4 border-primary" : ""}`}
                        onClick={() => {
                          setSelectedMessage(message)
                          markAsRead(message.id)
                        }}
                      >
                        <div className="flex items-start justify-between mb-2">
                          <div className="flex items-center gap-2">
                            <Avatar className="h-8 w-8">
                              <AvatarImage src={message.customer.avatar} />
                              <AvatarFallback>{message.customer.name.charAt(0)}</AvatarFallback>
                            </Avatar>
                            <div>
                              <p className="font-medium text-sm">{message.customer.name}</p>
                              <p className="text-xs text-muted-foreground">{message.timestamp}</p>
                            </div>
                          </div>
                          <div className="flex gap-1">
                            <Badge variant="secondary" className={`text-xs ${getPriorityColor(message.priority)}`}>
                              {message.priority}
                            </Badge>
                            {message.status === "unread" && (
                              <div className="h-2 w-2 bg-primary rounded-full"></div>
                            )}
                          </div>
                        </div>
                        <p className="text-sm font-medium mb-1 line-clamp-1">{message.subject}</p>
                        <p className="text-xs text-muted-foreground line-clamp-2">{message.message}</p>
                        {message.orderId && (
                          <Badge variant="outline" className="mt-2 text-xs">
                            {message.orderId}
                          </Badge>
                        )}
                      </div>
                    ))}
                  </div>
                </CardContent>
              </Card>
            </div>

            {/* Message Detail */}
            <div className="lg:col-span-2">
              {selectedMessage ? (
                <Card>
                  <CardHeader>
                    <div className="flex items-center justify-between">
                      <div className="flex items-center gap-3">
                        <Avatar className="h-10 w-10">
                          <AvatarImage src={selectedMessage.customer.avatar} />
                          <AvatarFallback>{selectedMessage.customer.name.charAt(0)}</AvatarFallback>
                        </Avatar>
                        <div>
                          <CardTitle className="text-lg">{selectedMessage.customer.name}</CardTitle>
                          <p className="text-sm text-muted-foreground">{selectedMessage.customer.email}</p>
                        </div>
                      </div>
                      <div className="flex gap-2">
                        <Button variant="ghost" size="sm">
                          <Reply className="h-4 w-4" />
                        </Button>
                        <Button variant="ghost" size="sm">
                          <Forward className="h-4 w-4" />
                        </Button>
                        <Button variant="ghost" size="sm" onClick={() => deleteMessage(selectedMessage.id)}>
                          <Trash2 className="h-4 w-4" />
                        </Button>
                      </div>
                    </div>
                  </CardHeader>
                  <CardContent>
                    <div className="space-y-4">
                      <div>
                        <h3 className="font-medium mb-2">{selectedMessage.subject}</h3>
                        <div className="flex gap-2 mb-4">
                          <Badge variant="secondary" className={getStatusColor(selectedMessage.status)}>
                            {selectedMessage.status}
                          </Badge>
                          <Badge variant="secondary" className={getPriorityColor(selectedMessage.priority)}>
                            {selectedMessage.priority} priority
                          </Badge>
                          {selectedMessage.orderId && (
                            <Badge variant="outline">
                              {selectedMessage.orderId}
                            </Badge>
                          )}
                        </div>
                        <p className="text-muted-foreground">{selectedMessage.message}</p>
                        <p className="text-sm text-muted-foreground mt-4">{selectedMessage.timestamp}</p>
                      </div>

                      {selectedMessage.rating && (
                        <div className="flex items-center gap-2 p-3 bg-muted rounded-lg">
                          <Star className="h-4 w-4 fill-yellow-400 text-yellow-400" />
                          <span className="font-medium">{selectedMessage.rating}.0</span>
                          <span className="text-sm text-muted-foreground">Customer Rating</span>
                        </div>
                      )}

                      {/* Reply Box */}
                      <div className="border-t pt-4">
                        {!showReplyBox ? (
                          <Button onClick={() => setShowReplyBox(true)} className="w-full">
                            <Reply className="mr-2 h-4 w-4" />
                            Reply to Message
                          </Button>
                        ) : (
                          <div className="space-y-3">
                            <Textarea
                              placeholder="Type your reply..."
                              value={replyText}
                              onChange={(e) => setReplyText(e.target.value)}
                              className="min-h-[100px]"
                            />
                            <div className="flex gap-2">
                              <Button variant="outline" size="sm">
                                <Paperclip className="mr-2 h-4 w-4" />
                                Attach File
                              </Button>
                              <div className="flex-1"></div>
                              <Button variant="outline" onClick={() => setShowReplyBox(false)}>
                                Cancel
                              </Button>
                              <Button onClick={sendReply}>
                                <Send className="mr-2 h-4 w-4" />
                                Send Reply
                              </Button>
                            </div>
                          </div>
                        )}
                      </div>
                    </div>
                  </CardContent>
                </Card>
              ) : (
                <Card>
                  <CardContent className="flex items-center justify-center h-96">
                    <div className="text-center">
                      <Mail className="h-12 w-12 text-muted-foreground mx-auto mb-4" />
                      <p className="text-muted-foreground">Select a message to view details</p>
                    </div>
                  </CardContent>
                </Card>
              )}
            </div>
          </div>
        </main>
      </div>
    </div>
  )
}
